.feed12 {
  z-index: 2;
  overflow: hidden;
  display: flex;
  -webkit-box-align: center;
  align-items: center;
  -webkit-box-pack: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.3);
}

.feed21 {
  background: rgba(0, 0, 0, 0.25);
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.feed22 {
  /* margin: 0px 0px 4rem; */
  margin-top: 60px;
  background-color: rgb(255, 255, 255);
  border: 1px solid rgb(247, 248, 250);
  box-shadow: rgb(47 128 237 / 5%) 0px 4px 8px 0px;
  padding: 0px;
  width: 50vw;
  /* overflow: hidden auto; */
  align-self: center;
  max-width: 420px;
  max-height: auto;
  min-height: auto;
  /* display: flex; */
  border-radius: 20px;
  position: relative;
}
.feed23 {
  padding: 25px 20px;
  border-bottom: 1px solid rgb(206, 208, 217);
  position: sticky;
  background-color: #fff;
  z-index: 10;
  top: 0;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  /* height: 300px; */
}
.feed24 {
  box-sizing: border-box;
  margin: 0px;
  min-width: 0px;
  font-weight: 500;
  font-size: 16px;
}
.feed25 {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}
.feed26 {
  position: relative;
  display: flex;
  padding: 15px;
  -webkit-box-align: center;
  align-items: center;
  width: 100%;
  white-space: nowrap;
  background: none;
  outline: none;
  border-radius: 20px;
  color: rgb(0, 0, 0);
  border: 1px solid rgb(206, 208, 217);
  appearance: none;
  font-size: 18px;
  transition: border 100ms ease 0s;
  margin-bottom: 10px;
}
.feed26:focus {
  border: 1px solid rgb(232, 0, 111);
  outline: none;
}
.feed27 {
  /* width:18px;
    height:18px;
