@media (max-width: 600px) {
  .feed22 {
    width: 95vw;
  }
}
@media (max-width: 460px) {
  .Gas2 {
    display: flex;
    flex-direction: column;
    padding: 0;
    width: 80px;
  }
  .ada {
    font-size: 12px;
    width: 46px;
  }
  .Enable {
    font-size: 14px;
  }
  .Enable4 {
    font-size: 14px;
  }
}

@media (min-width: 461px) and (max-width: 767px) {
  .ada {
    width: 50px;
    box-sizing: border-box;
  }
  .Gas2 {
    width: 110px;
  }
  .Enable4 {
    font-size: 16px;
  }
}

@media (max-width: 767px) {
  .send1 {
    font-size: 14px;
  }
  .Trade4 {
    width: 100%;
    padding: 25px 7px;
  }

  .Trade2 {
    padding: 10px 5px;
