@media screen and (max-width: 767px) {
  .desktop_view_cont {
    display: none;
  }
  .mobile_view_header_cont {
    position: fixed;
    top: 0;
    height: 100vh;
    width: 100vw;
    background: #2a4635cc;
    z-index: 10000;
    left: 0;
    backdrop-filter: blur(20px);
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    gap: 10px;
  }
  .header_menu_icon_cont {
    display: block;
  }
  .mobile_view_header_cont_head_body {
    width: 100%;
    display: flex;
    flex-direction: column;
    color: rgb(0, 0, 0);
    gap: 10px;
    align-items: flex-end;
    padding: 1em;
  }
  .dark .mobile_view_header_cont_head_body {
    color: #fff;
  }
}
@media screen and (max-width: 500px) {
  .wallet-id {
    display: flex;
    align-items: center;
    grid-gap: 5px;
    gap: 5px;
    font-size: 12px;
