.stakingTable_table {
  box-sizing: border-box;
  margin: 0px auto;
  min-width: 0px;
  width: 100%;
  /* max-width: 1232px; */
  display: table;
  padding: 0px;
  /* border-collapse: separate;
  border-spacing: 0px 9px; */
  margin-top: 0px;
}
.stakingTable_titles {
  box-sizing: border-box;
  margin: 0px;
  min-width: 0px;
  display: table-header-group;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 1;
}
.stakingTable_title_div {
  box-sizing: border-box;
  margin: 0px;
  min-width: 0px;
  background: #d0d0d0;
}
.stakingTable_heading_titles {
  box-sizing: border-box;
  margin: 0px;
  min-width: 0px;
  padding: 10px;
  padding-left: 16px;
  padding-right: 16px;
  color: #606060;
  font-size: 13px;
  text-align: right;
  font-weight: 400;
  white-space: nowrap;
  text-align: center;
}
.stakingTable_heading_titles_first {
  position: -webkit-sticky;
  position: sticky;
  left: 0;
  /* z-index: 100; */
  background: inherit;
  text-align: left;
  font-size: 13px;
}
.stakingTable_body {
  box-sizing: border-box;
  margin: 0px;
  min-width: 0px;
  width: 100%;
}
.stakingTable_body_row {
  box-sizing: border-box;
  /* margin: 0px 0px 16px; */
  min-width: 0px;
  display: table-row;
  flex-direction: column;
  background: white;
  /* border-radius: 8px; */
  transition: box-shadow 0.2s ease-in-out 0s, -webkit-transform 0.2s ease-in-out 0s;
  transition: transform 0.2s ease-in-out 0s, box-shadow 0.2s ease-in-out 0s;
  transition: transform 0.2s ease-in-out 0s, box-shadow 0.2s ease-in-out 0s, -webkit-transform 0.2s ease-in-out 0s;
  cursor: pointer;
  font-size: 13px;
  border-bottom: solid 1px #28372f;
}
.stakingTable_body_row_data {
  box-sizing: border-box;
  margin: 0px;
  min-width: 0px;
  padding: 16px;
  display: table-cell;
  justify-content: space-between;
  white-space: nowrap;
  text-align: center;
}
.stakingTable_body_row_data_first {
  position: -webkit-sticky;
  position: sticky;
  left: 0;
  /* z-index: 100; */
  background: inherit;
  text-align: left;
}

.stakingTable_body_row:hover {
  box-shadow: rgb(0 0 0 / 15%) 0px 0px 10px;
  -webkit-transform: scaleX(0.99);
          transform: scaleX(0.99);
}

