.Ping,
.Ping .c-basePart {
  position: absolute;
  height: 0.35rem;
  width: 0.35rem;
  right: 23%;
  left: 67%;
  top: 12%;
}

.Ping .c-basePart,
.Ping .c-flashingPart {
  border-radius: 9999px;
  background-color: #229e54;
}

.Ping .c-flashingPart {
  animation: ping 1s cubic-bezier(0, 0, 0.2, 1) infinite;
  position: absolute;
  display: inline-flex;
  height: 100%;
  width: 100%;
  left: 74%;
  top: 8%;
  opacity: 0.75;
}
.dark .c-basePart,
.dark .c-flashingPart {
  background-color: #e7b02e;
}
.animate-ping {
  -webkit-animation: ping 1s cubic-bezier(0, 0, 0.2, 1) infinite;
  animation: ping 1s cubic-bezier(0, 0, 0.2, 1) infinite;
}

@-webkit-keyframes ping {
  75%,
  to {
    -webkit-transform: scale(2);
    transform: scale(2);
    opacity: 0;
  }
}

@keyframes ping {
  75%,
  to {
    -webkit-transform: scale(2);
    transform: scale(2);
    opacity: 0;
  }
}
